import {DomainObject} from "./DomainObject";

export class SecurityLevelConfiguration extends DomainObject{
    public uid: number;
    public emailAsUserName: boolean;
    public registrationVerificationRequired: boolean;
    public minimumPasswordLength: number;
    public validPeriod: number;
    public includeSpecialCharacters: boolean;
    public includeNumbersAndLetters: boolean;
    public excludeRepeatingCharacters: boolean;
    public allowMixedCase: boolean;
    public excludeUserNameAndEmail: boolean;
    public checkMinimumLength: boolean;
    public checkValidPeriod: boolean;
    public checkLoginAttempts: boolean;
    public loginAttemptsBeforeCaptcha: number;

    constructor(data?: any){
        super(data);

        if(data){
            this.emailAsUserName= data.emailAsUserName;
            this.registrationVerificationRequired = data.registrationVerificationRequired;
            this.minimumPasswordLength = data.minimumPasswordLength;
            this.validPeriod = data.validPeriod;
            this.includeSpecialCharacters = data.includeSpecialCharacters;
            this.includeNumbersAndLetters = data.includeNumbersAndLetters;
            this.excludeRepeatingCharacters = data.excludeRepeatingCharacters;
            this.allowMixedCase = data.allowMixedCase;
            this.excludeUserNameAndEmail = data.excludeUserNameAndEmail;
            this.checkMinimumLength = data.checkMinimumLength;
            this.checkValidPeriod = data.checkValidPeriod;
            this.checkLoginAttempts = data.checkLoginAttempts;
            this.loginAttemptsBeforeCaptcha = data.loginAttemptsBeforeCaptcha;
        }
    }
    
    public prepareData(): void {}
}


