import {Injectable} from "@angular/core";
import {CanActivate, Router, ActivatedRouteSnapshot, RouterStateSnapshot} from "@angular/router";
import {AccessToken} from "../../models/AccessToken";

@Injectable()
export class AuthGuard implements CanActivate {

    static currentUserToken: AccessToken = null;
    static _router: Router = null;

    constructor(private router: Router) {
        AuthGuard._router = this.router;
    }

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        if (AuthGuard.currentUserToken) {
            return true;
        }
        if (localStorage.getItem('currentUser')) {
            AuthGuard.currentUserToken = JSON.parse(localStorage.getItem('currentUser'));
            return true;
        }

        this.router.navigate(['auth', 'login'], {queryParams: {returnUrl: state.url}});

        return false;
    }

    static storeCurrentUserToken(accessToken: AccessToken) {
        this.currentUserToken = accessToken;
    }

    static logout() {
        localStorage.removeItem('currentUser');
        this.currentUserToken = null;
    }

    static loginExceeded() {
        this.navigate({error : 'loginExceeded', fresh: new Date().getTime()});
    }

    static navigate(param: any) {
        if (this._router)
            this._router.navigate(['auth', 'login'], {queryParams: param})
    }
}



