import {Injectable, Inject} from "@angular/core";
import "rxjs/add/observable/of";
import "rxjs/add/operator/do";
import "rxjs/add/operator/delay";
import {Http, RequestOptions, Headers, Response, URLSearchParams} from "@angular/http";
import {AppProperties} from "../app.properties";
import {Observable} from "rxjs";
import {SecurityLevelConfiguration} from "../models/SecurityLevelConfiguration";
import {UserCredentials} from "../models/UserCredentials";
import {SecretQuestion} from "../models/SecretQuestion";
import {AbstractService} from "../services/abstract-service";
import {AccessToken} from "../models/AccessToken";
import {SessionUtil} from "./+login/session-util/session-util";

@Injectable()
export class AuthService extends AbstractService {
    isLoggedIn: boolean = false;
    private oauthUrl: string = '';
    private oauthClientId: string = '';
    private oauthClientSecret: string = '';
    private oauthClientLogoutUrl: string = '';

    constructor(private http: Http, @Inject(AppProperties) appProps: AppProperties) {
        super(appProps);
        this.oauthUrl = appProps.get(AppProperties.OAUTH_URL_KEY);
        this.oauthClientId = appProps.get(AppProperties.OAUTH_CLIENT_ID);
        this.oauthClientSecret = appProps.get(AppProperties.OAUTH_CLIENT_SECRET);
        this.oauthClientLogoutUrl = appProps.get(AppProperties.OAUTH_LOGOUT_URL_KEY);

    }

    login(username: string, password: string): Observable<AccessToken> {
        let data = new URLSearchParams();
        data.append('username', username);
        data.append('password', password);
        data.append('grant_type', "password");
        
        return this.http.post(this.oauthUrl, data, this.getLoginRequestOptions())
            .map(this.getAccessToken)
            .catch(this.handleError);
    }

    verifyCaptcha(reCaptchaResponse: string): Observable<any> {
        return this.http.post(this.apiBaseUrl + '/auth/recaptcha?re-capthca-response=' + reCaptchaResponse, new RequestOptions({
            headers: this.getAnonymousHeaders()
        }))
            .catch(this.handleError);
    }

    private getAccessToken(res: Response): AccessToken {
        return new AccessToken(res.json() || {});
    }

    private getUserCredentials(res: Response): UserCredentials {
        return new UserCredentials(res.json() || {});
    }

    getSecurityConfiguration(): Observable<SecurityLevelConfiguration> {
        return this.http.get(this.apiBaseUrl + '/auth/security-configuration', new RequestOptions({
            headers: this.getAnonymousHeaders()
        }))
            .map(this.extractSecurityConfiguration)
            .catch(this.handleError);
    }

    getUserSecretQuestion(email: string): Observable<SecretQuestion> {
        let params : URLSearchParams = new URLSearchParams();
        params.set("email", email);
        return this.http.get(this.apiBaseUrl + '/auth/login/password', new RequestOptions({
            search: params,
            headers: this.getAnonymousHeaders()
        }))
            .map(this.extractSecretQuestion)
            .catch(this.handleError);
    }

    requestResetPassword(email: string, answer: string) {
        let params : URLSearchParams = new URLSearchParams();
        params.set("email", email);
        return this.http.post(this.apiBaseUrl + '/auth/login/password', {"answer": answer}, new RequestOptions({
            search: params,
            headers: this.getAnonymousHeaders()
        })).catch(this.handleError)
    }

    checkLoginUser(force: boolean){
        let params : URLSearchParams = new URLSearchParams();
        if (force)
            params.set("force-login", "true");
        return this.http.get(this.apiBaseUrl + '/auth/login', new RequestOptions({
            search: params,
            headers: this.getStandardHeaders()
        }))
            .map(this.extractUserCredentials)
            .catch(this.handleError);
    }

    private extractUserCredentials(res: Response): any {
        let userInfo = (res.json() || {}) as UserCredentials;
        return userInfo;
    }

    private getLoginRequestOptions() {
        return new RequestOptions({
            headers: this.getLoginHeaders()
        });
    }

    private getLoginHeaders() {
        let basicAuthHeader = btoa(this.oauthClientId + `:` + this.oauthClientSecret);
        let headers = new Headers();
        // @todo This will not be necessary when the user actually logs in and the
        //       and the server session has been established.
        headers.append('Authorization', `Basic  ${basicAuthHeader}`);
        headers.append('Accept', `application/json`);
        // OAuth2 specification requires application/x-www-form-urlencoded content type
        headers.append('Content-Type', `application/x-www-form-urlencoded`);
        return headers;
    }

    public handleError(error: Response) {
        return Observable.throw(error.text() && error.json() || {message: 'Server error', status: error.status});
    }

    private extractSecurityConfiguration(res: Response): SecurityLevelConfiguration {
        return new SecurityLevelConfiguration(res.json() || {});
    }

    private extractSecretQuestion(res: Response): SecretQuestion {
        return new SecretQuestion(res.json() || {});
    }

    logout() {
        SessionUtil.instance.stopAndReset();
        return this.http.get(this.oauthClientLogoutUrl, this.getStandardRequestOptions());
    }
}



