/**
 * Base class for all domain objects/models
 */
export abstract class DomainObject {
    public uid: number;
    
    constructor(data?: any) {
        if (data) {
            this.uid = data.uid;
        }
    }
    
    /**
     * Exchanges data from one object to this domain object
     * @param data The object used to copy data from
     */
    public exchangeData(data: {}): any {
        if (data) {
            Object.assign(this, data);
        }
    }
    
    /**
     * Creates a clone of this object.
     * 
     * @todo Find a solution where generics are not needed in order to construct
     * a clone.
     * 
     * @param clazz The generic class to used to construct or object.
     * @return <T>
     */
    public clone<T extends DomainObject>(clazz: {new(data?: any): T;}): T {
        if (clazz.name !== this.constructor.name) {
            throw new Error(`Cannot clone ${this.constructor.name} to ${clazz.name}`);
        }
        return new clazz(JSON.parse(JSON.stringify(this)));
    }
    
    /**
     * Splits dates in the following format: yyyy-MM-dd'T'HH:mm:ss.SSS
     */
    public splitDateTime(dt:string) {
        return dt.split("T");
    }
    
    /**
     * Prepares data to insure it is in the right structure before submitting
     * to a web service endpoint
     */
    public abstract prepareData(): void;
}





