import {DomainObject} from "./DomainObject";


export class SecretQuestion extends DomainObject{
    public uid: number;
    public question: string;

    constructor(data?: any){
        super(data);
        if(data){
            this.question = data.question;
        }
    }
    
    public prepareData(): void {}
}


