import {Injectable, Inject} from '@angular/core';
import {Observable, Subject} from "rxjs/Rx";

import {JsonApiService} from "../../core/api/json-api.service";
import {AbstractService} from "../../services/abstract-service";
import {AppProperties} from "../../app.properties";
import {UserCredentials} from "../../models/UserCredentials";
import {Http, Response} from "@angular/http";

@Injectable()
export class UserService extends AbstractService {

  public user: Subject<any> = new Subject();

  public userInfo = {
    username: 'Guest'
  };

  constructor(private http: Http, @Inject(AppProperties) appProps: AppProperties) {
    super(appProps);
  }

  getLoginInfo():Observable<any> {
    return this.http.get(this.apiBaseUrl + '/auth/login', this.getStandardRequestOptions())
        .map(this.extractOne)
        .catch(this.handleError);
  }

  private extractOne(res: Response): any {
    this.userInfo = (res.json() || {}) as UserCredentials;
    return this.userInfo;
  }

}



