import {Injectable, Inject} from "@angular/core";
import {AppProperties} from "../app.properties";
import {Response, Headers, RequestOptions} from "@angular/http";
import {Observable} from "rxjs/Rx";
import {AuthGuard} from "../core/guards/auth-guard";
import {NotificationService} from "../shared/utils/notification.service";
import {LoginComponent} from "../+auth/+login/login.component";
import {accessSync} from "fs";

@Injectable()
export abstract class AbstractService {

    protected apiBaseUrl: string;
    protected ediUploadCsvURL: string;
    protected ediUploadCuscarURL: string;

    /**
     * Creates an instance of AbstractService.
     * @param {AppProperties} appProps
     *
     * @memberOf AbstractService
     */
    constructor(@Inject(AppProperties) appProps: AppProperties) {
        this.apiBaseUrl = appProps.get(AppProperties.API_BASE_URL_KEY);
        this.ediUploadCsvURL = appProps.get(AppProperties.EDI_UPLOAD_CSV);
        this.ediUploadCuscarURL = appProps.get(AppProperties.EDI_UPLOAD_CUSCAR_KEY);
    }

    /**
     * Returns common request options
     *
     * @returns
     *
     * @memberOf AbstractService
     */
    public getStandardRequestOptions() {
        return new RequestOptions({
            headers: this.getStandardHeaders(),
            // withCredentials: true
        });
    }

    /**
     * Returns standard headers
     *
     * @returns
     *
     * @memberOf AbstractService
     */
    public getStandardHeaders() {
        let token_type = AuthGuard.currentUserToken ? AuthGuard.currentUserToken.token_type : '';
        let access_token = AuthGuard.currentUserToken ? AuthGuard.currentUserToken.access_token : '';
        let headers = new Headers();
        headers.append('Authorization', `${token_type} ${access_token}`);
        headers.append('Accept', `application/json`);
        headers.append('Content-Type', `application/json`);
        return headers;
    }

    /**
     * Handles error responses
     *
     * @param {Response} error
     * @returns
     *
     * @memberOf AbstractService
     */
    public handleError(error: Response) {
        /* @todo Create model to represent a web service error */
        console.error(error);

        if (error.status == 401) {
            AbstractService.handleCommonError(error);
        }
        return Observable.throw(error.json().errors || 'Server error')
    }

    private static handleCommonError(error: Response) {
        if (error.status == 401 && error.json()) {
            var errorJson = error.json();
            if (errorJson.error == "maximum_sessions") {
                AuthGuard.loginExceeded();
            } else if (errorJson.error == 'invalid_token') {
                NotificationService.instance.smartMessageBox({
                    title: "<i class='fa fa-warning txt-color-red'></i> Session Ended",
                    content: "Your Session has ended unexpectedly. Please sign in again to continue working.",
                    buttons: '[Ok]'
                }, (btn) => {
                    AuthGuard.navigate({});
                });
            }
        }
    }

    /**
     * Returns request options for anonymous requests
     *
     * @returns
     *
     * @memberOf AbstractService
     */
    public getAnonymousRequestOptions() {
        return new RequestOptions({
            headers: this.getAnonymousHeaders()
        });
    }

    /**
     * Returns headers for anonymous requests
     *
     * @returns
     *
     * @memberOf AbstractService
     */
    public getAnonymousHeaders() {
        let headers = new Headers();
        headers.append('Accept', `application/json`);
        headers.append('Content-Type', `application/json`);
        return headers;
    }
}


