import {NotificationService} from "../../../shared/utils/notification.service";
import {EventEmitter} from "@angular/core";
import any = jasmine.any;

export class SessionUtil{

    public static instance : SessionUtil = new SessionUtil(NotificationService.instance);

    public static sessionTimeInSeconds : number; //in seconds
    public idleTimeInSeconds : number; //in seconds

    public static idleHanlder: EventEmitter<any> = new EventEmitter();

    private static actualIdleTime : number = 0;

    private static secondsToText: string ='00';

    private static minutesToText: string ='00';

    private seconds : number = 0;
    private minutes : number = 0;
    private hours : number = 0;

    private secondsText: string = '00';
    private minutesText: string = '00';
    private hoursText : string = '00';

    private timeText : string;

    private intervalCounterId : any;

    private intervalCounterIdle: any;

    private sessionTimeoutMessageShown: boolean = false;

    private static isShowingMessage: boolean = false;

    private static isShowingIdleMessage: boolean = false;

    private cases : string [] = ['click','mousemove','keydown','mousewheel','mousedown', 'touchstart', 'touchmove'];

    constructor(private notificationService: NotificationService){

    }

    public start(){
        this.splitTimeInConteners();
        this.startCounter();
        this.putCheckIdle();
    }

    private splitTimeInConteners(){
        if(SessionUtil.sessionTimeInSeconds/60 > 1){
            this.minutes = Math.floor(SessionUtil.sessionTimeInSeconds/60);
            this.seconds = SessionUtil.sessionTimeInSeconds%60;
            if(this.minutes/60 > 1){
                this.hours = Math.floor(this.minutes/60);
                this.minutes = Math.floor(this.minutes%60);
                this.seconds = this.minutes%60;
            }
        }else{
            this.seconds = SessionUtil.sessionTimeInSeconds;
            this.minutes = 0;
            this.hours = 0;
        }
        this.convertTimeInContenersToText();
        this.timeText = this.hoursText + ':' +this.minutesText;
    }

    private convertTimeInContenersToText(){
        if(this.seconds < 10){
            SessionUtil.secondsToText = '0'+this.seconds.toString();
        }else{
            SessionUtil.secondsToText = this.seconds.toString();
        }

        if(this.minutes < 10){
            SessionUtil.minutesToText = '0'+this.minutes.toString();
        }else{
            SessionUtil.minutesToText = this.minutes.toString();
        }

        if(this.hours < 10){
            this.hoursText = '0'+this.hours.toString();
        }else{
            this.hoursText = this.hours.toString();
        }

    }

    private startCounter(): void{
        this.intervalCounterId = setInterval(() => this.tick(),1000);
    }

    private tick(): void{
        if(--SessionUtil.sessionTimeInSeconds === 0){
            this.showMessage();
            this.stopCounter();
        }else{
            if((SessionUtil.sessionTimeInSeconds+1)/60 >= 1){
                SessionUtil.minutesToText = '0'+ Math.floor((SessionUtil.sessionTimeInSeconds+1)/60).toString();
                let aux: number = (SessionUtil.sessionTimeInSeconds+1)%60;
                if(aux < 10){
                    SessionUtil.secondsToText = '0' + aux.toString();
                }else{
                    SessionUtil.secondsToText = aux.toString();
                }
            }else{
                SessionUtil.minutesToText = '00';
                if (SessionUtil.sessionTimeInSeconds < 10) {
                    SessionUtil.secondsToText = '0' + SessionUtil.sessionTimeInSeconds.toString();
                } else {
                    SessionUtil.secondsToText = SessionUtil.sessionTimeInSeconds.toString();
                }
            }
            if (SessionUtil.sessionTimeInSeconds < 10 * 60) {
                this.showMessage();
            }
        }
    }

    private showMessage(){
        if (!this.sessionTimeoutMessageShown) {
            this.notificationService.smartMessageBox({
                title: "<span id='session-timeout-title'><i class='fa fa-warning txt-color-red'></i> YOUR SESSION WILL BE TERMINATED</span>",
                content: "<span id='session-timeout-text'>Your session will terminate in <span id='session-timeout-counter'>" + "00:"+SessionUtil.minutesToText + ":" + SessionUtil.secondsToText + "</span> seconds. Please save your work and exit the application.</span>",
                buttons: '[OK]'
            }, (btn)=> {
               this.btnAction();
            });
            this.sessionTimeoutMessageShown = true;
            SessionUtil.isShowingMessage = true;
        } else {
            jQuery('#session-timeout-counter').html("00:"+SessionUtil.minutesToText + ":" + SessionUtil.secondsToText);
        }

        if(SessionUtil.actualIdleTime == this.idleTimeInSeconds && SessionUtil.isShowingMessage){
            this.stopCounter();
            this.stopIdleCounter();
            jQuery('#session-timeout-title').html("<i class='fa fa-warning txt-color-red'></i> SESSION TIMEOUT");
            jQuery('#session-timeout-text').html("Your session has timed out due to inactivity. Please sign in again to continue working.");
            SessionUtil.isShowingIdleMessage = true;
        }else if(SessionUtil.actualIdleTime == this.idleTimeInSeconds && !SessionUtil.isShowingIdleMessage){
            this.stopCounter();
            this.stopIdleCounter();
            SessionUtil.isShowingIdleMessage = true;
            this.notificationService.smartMessageBox({
                title: "<i class='fa fa-warning txt-color-red'></i> SESSION TIMEOUT",
                content: "Your session has timed out due to inactivity. Please sign in again to continue working.",
                buttons: '[OK]'
            }, (btn)=> {
                if ('OK' == btn) {
                    SessionUtil.idleHanlder.emit("endSession");
                }
            });
        }else if(SessionUtil.sessionTimeInSeconds === 0 && this.sessionTimeoutMessageShown && SessionUtil.isShowingMessage){
            jQuery('#session-timeout-title').html("<i class='fa fa-warning txt-color-red'></i> SESSION ENDED");
            jQuery('#session-timeout-text').html("Your session has ended unexpectedly. Please sign in again to continue working.");
        }else if(SessionUtil.sessionTimeInSeconds === 0 && !SessionUtil.isShowingMessage){
            this.notificationService.smartMessageBox({
                title: "<i class='fa fa-warning txt-color-red'></i> SESSION ENDED",
                content: "Your session has ended unexpectedly. Please sign in again to continue working.",
                buttons: '[OK]'
            }, (btn)=> {
                SessionUtil.idleHanlder.emit("endSession");
            });
        }

    }

    private btnAction(){
        if(SessionUtil.sessionTimeInSeconds === 0 && this.sessionTimeoutMessageShown){
            SessionUtil.idleHanlder.emit("endSession");
        }else{
            SessionUtil.isShowingMessage = false;
        }
    }

    private stopCounter(): void{
        clearInterval(this.intervalCounterId);
        SessionUtil.sessionTimeInSeconds = 0;
    }


    private putCheckIdle(){
        for(let a of this.cases){
            document.body.addEventListener(a,this.resetIdleTime,true);
        }
        this.startCountingIdleTime();
    }

    private resetIdleTime(){
        SessionUtil.actualIdleTime = 0;
    }

    private startCountingIdleTime(){
        this.intervalCounterIdle = setInterval(() => this.checkIdle(),1000);
    }

    private checkIdle(){
        if(SessionUtil.actualIdleTime == this.idleTimeInSeconds){
            this.showMessage();
        }else{
            SessionUtil.actualIdleTime++;
        }
    }

    private stopIdleCounter(){
        clearInterval(this.intervalCounterIdle);
        SessionUtil.actualIdleTime = 0;
        for(let a of this.cases){
            document.body.removeEventListener(a,this.resetIdleTime,true);
        }
    }

    public stopAndReset(){
        SessionUtil.sessionTimeInSeconds = 0;
        this.idleTimeInSeconds = 0;
        this.stopIdleCounter();
        this.stopCounter();

        this.seconds = 0;
        this.minutes = 0;
        this.hours = 0;

        this.secondsText = '00';
        this.minutesText = '00';
        this.hoursText = '00';

        this.timeText = '';

        this.sessionTimeoutMessageShown = false;

        SessionUtil.isShowingMessage = false;
        SessionUtil.isShowingIdleMessage = true;
    }
}


