import {Injectable} from '@angular/core';

declare var $: any;

@Injectable()
export class NotificationService {

  public static instance : NotificationService = new NotificationService();

  constructor() {
  }

    /**
     * Shows a small notification box
     * @param data - Box options
     * @param cb - Callback function
     */
    smallBox(data: {
        title: string,
        content: string,
        icon?: any,
        iconSmall?: any,
        sound?: any,
        sound_file?: any,
        color?: any,
        timeout?: any,
        colortime?: number,
        colors?: any
    }, cb?) {
        $.smallBox(data, cb)
    }

    /**
     * Shows a large notification box
     * @param data - Box options
     * @param cb - Callback function
     */
    bigBox(data: {
        title: string,
        content: string,
        icon?: any,
        number?: any,
        color?: any,
        sound?: any,
        sound_file?: string,
        timeout?: any,
        colortime?: number,
        colors?: any
    }, cb?) {
        $.bigBox(data, cb)
    }

    /**
     * Shows a smart message box
     * @param data - Box options
     * @param cb - Callback function
     */
    smartMessageBox(data: {
        title: string,
        content: string,
        NormalButton?: any,
        ActiveButton?: any,
        buttons?: any,
        input?: any,
        inputValue?: any,
        placeholder?: string,
        options?: any
    }, cb?) {
        $.SmartMessageBox(data, cb)
    }

}



